import * as React from "react";
import Box from "@mui/material/Box";
import LinearProgress from "@mui/material/LinearProgress";

export type ProgressBarState = { value: number; min: number; max: number,};
export const defaultProgressBarState = { value: 0, min: 0, max: 0 };

const normalize = (progressBarState: ProgressBarState) =>
  ((progressBarState.value - progressBarState.min) * 100) /
  (progressBarState.max - progressBarState.min);

export default function ProgressBar(props: ProgressBarState) {
  const [progress, setProgress] = React.useState(0);

  React.useEffect(() => {
    const timer = setInterval(() => {
      setProgress((oldProgress) => {
        if (oldProgress === 100) {
          return 0;
        }
        const diff = Math.random() * 10;
        return Math.min(oldProgress + diff, 100);
      });
    }, 500);

    return () => {
      clearInterval(timer);
    };
  }, []);

  return (
    <Box sx={{ width: "100%" }}>
      <LinearProgress variant="determinate" value={normalize(props)} />
    </Box>
  );
}
